.App {
  text-align: center;
}

.clicked {
  background-color: aqua;
}

h1 {
  font-size: 1.5rem;
}

.buttonContainer {
  z-index: 0;
}

.button-1 {
  position: absolute;
  top: 50%;
  left: 15%;
  height: 4rem;
  width: 7rem;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px black;
  background-color: white;
  transition: all 0.07s linear 0s;
}

.button-2 {
  position: absolute;
  top: 30%;
  left: 15%;
  height: 4rem;
  width: 7rem;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px black;
  background-color: white;
  transition: all 0.07s linear 0s;
}

.button-3 {
  position: absolute;
  top: 50%;
  left: 55%;
  height: 4rem;
  width: 7rem;
  font-size: 0.75rem;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px black;
  background-color: white;
  transition: all 0.07s linear 0s;
}

.button-4 {
  position: fixed;
  top: 30%;
  left: 55%;
  height: 4rem;
  width: 7rem;
  font-size: 0.75rem;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px black;
  background-color: white;
  transition: all 0.07s linear 0s;
}

.show {
  opacity: 1;
  background-color: rgb(42, 149, 165);
  z-index: 10;
}

.hidden {
  opacity: 0;
}
